.card-similar {
	margin: 0 0 20px;
	&__heading {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 0 0 30px;
	}
	&__link {
		font-weight: 600;
		font-size: 18px;
		line-height: 160%;
		color: $color-dark-orange;
		@include vp-1023 {
			display: none;
		}
		&::after {
			background-color: $color-dark-orange;
		}
	}
	&__card {
		display: block;
		padding: 20px 20px 30px;
		background-color: $color-default-white;
		border: 1px solid $color-lavender;
		border-radius: 10px;
		transition: box-shadow $trans-default;
		@include vp-1023 {
			border: 0;
			padding: 0;
		}
		@media (hover: hover), screen and (min-width: 0\0) {
			&:hover,
			&:focus-visible {
				box-shadow: 0px 38px 128px rgba(45, 49, 66, 0.025), 0px 15.0165px 53.4753px rgba(45, 49, 66, 0.02), 0px 8.57474px 28.5905px rgba(45, 49, 66, 0.015), 0px 5.32214px 16.0276px rgba(45, 49, 66, 0.0175), 0px 3.15228px 8.51213px rgba(45, 49, 66, 0.01), 0px 1.45895px 3.54209px rgba(45, 49, 66, 0.0075);
			}
		}
		&-pic {
			width: 100%;
			height: 160px;
			position: relative;
			margin: 0 0 15px;
			@include vp-1023 {
				&::after {
					content: "";
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					width: 50%;
					background-color: rgba($color-lavender, 0.5);
				}
			}
			img {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				z-index: 1;
			}
		}
		&-title {
			font-weight: 700;
			font-size: 14px;
			line-height: 122%;
			letter-spacing: 0.05em;
			margin: 0 0 20px;
			color: $color-dark-orange;
			@include vp-1023 {
				display: none;
			}
		}
		&-model {
			display: inline-block;
			font-weight: 600;
			font-size: 16px;
			line-height: 180%;
			color: $color-bunting;
			margin: 0 0 25px;
			@include vp-767 {
				margin: 0 0 15px;
			}
		}
		&-price {
			padding: 0 0 10px;
			font-weight: 700;
			font-size: 18px;
			line-height: 22px;
			&-time {
				@include vp-767 {
					font-weight: 400;
					font-size: 18px;
					line-height: 160%;
					color: $color-logan;
				}
			}
		}
		&-add {
			position: absolute;
			width: 56px;
			height: 44px;
			border-top-left-radius: 7px;
			border-bottom-left-radius: 7px;
			margin: 0;
			bottom: 20px;
			right: 0;
			transition: transform $trans-default;
			transform: translateX(100%);
			&-text {
				display: none;
			}
			&-icon {
				display: inline-flex;
				width: 100%;
				height: 100%;
				align-items: center;
				justify-content: center;
			}
		}
	}
	&__slider {
		&-nav {
			position: relative;
			@include vp-1023 {
				margin: 50px 0 0 0;
			}
		}
		&-btns {
			position: absolute;
			width: 75px;
			left: 100px;
			bottom: 20px;
		}
		&-btn {
			color: $color-bunting;
			&::after {
				display: none;
			}
			svg {
				color: $color-bunting;
				transition: color $trans-default;
				width: 20px;
				height: 20px;
			}
			&.swiper-button-disabled {
				opacity: 1;
				svg {
					color: $color-hawkes-blue;
				}
			}
			&--next {
				right: 0;
			}
			&--prev {
				left: 0;
			}
		}
	}
	.swiper-pagination-fraction {
		right: auto;
		bottom: 0;
		width: 70px;
		left: 0;
		text-align: center;
		font-weight: 700;
		font-size: 24px;
		line-height: 160%;
		color: $color-bunting;
		@include vp-1023 {
			font-size: 20px;
			text-align: left;
		}
	}
	.swiper-pagination-current {
		display: inline-block;
		width: 33%;
	}
	.swiper-pagination-total {
		font-size: 16px;
		@include vp-1023 {
			font-size: 14px;
		}
	}
	&--sale & {
		&__card {
			&-title {
				color: $color-apple;
			}
		}
	}
}
